import { PropsWithChildren } from "react"
import { ListSorterDirection } from "@rp/common/utils"
import { Box, Typography } from "@rp/react/web/mui/core"
import { ArrowDownward } from "@rp/react/web/mui/icons"

const HubSortableHeaderDesktopCell = ({
  children,
  direction,
  onDirectionChange,
  sortVisible,
  defaultDirection,
}: PropsWithChildren & {
  defaultDirection: ListSorterDirection
  direction: ListSorterDirection
  onDirectionChange?: (direction: ListSorterDirection) => void
  sortVisible: boolean
}) => {
  let transform = direction === "asc" ? "rotate(180deg)" : "rotate(0deg)"
  if (!sortVisible) transform = defaultDirection === "asc" ? "rotate(180deg)" : "rotate(0deg)"
  return (
    <Box
      sx={{
        userSelect: "none",
        display: "flex",
        alignItems: "center",
        cursor: "pointer",
        gap: 1,
        "&:hover svg": { opacity: sortVisible ? 1 : 0.3 },
      }}
      onClick={() => {
        if (sortVisible) {
          onDirectionChange?.(direction === "asc" ? "desc" : "asc")
        } else {
          onDirectionChange?.(defaultDirection)
        }
      }}
    >
      <Typography sx={{ fontSize: 11, fontWeight: 700 }}>{children}</Typography>
      <ArrowDownward
        sx={{
          transform,
          color: "text.secondary",
          fontSize: "20px",
          opacity: sortVisible ? 1 : 0,
        }}
      />
    </Box>
  )
}

export { HubSortableHeaderDesktopCell }
